.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    padding: 20px;
    color: white;
}

.App-header nav ul {
    list-style: none;
    padding: 0;
}

.App-header nav ul li {
    display: inline;
    margin: 0 10px;
}

.App-header nav ul li a {
    color: white;
    text-decoration: none;
}

.App-section {
    padding: 20px;
}

.App-footer {
    background-color: #282c34;
    color: white;
    padding: 10px;
    position: fixed;
    width: 100%;
    bottom: 0;
}